// Default
import React from "react";

// MUI components
import Container from "@mui/material/Container";
import Typography from "@mui/material/Typography";
import Box from "@mui/material/Box";
import Grid from "@mui/material/Grid";

// Utils
import { COMPANIES } from "../../utils/constant";

// Theme
import { useTheme } from "@mui/material/styles";
import useMediaQuery from "@mui/material/useMediaQuery";
import { customTheme } from "../../theme";

const MyJourney = () => {
  const theme = useTheme();
  const matches = useMediaQuery(
    theme.breakpoints.down(customTheme.breakpoints.values.md)
  );

  const data = [
    {
      year: "2016",
      doing: "Started my journey as a developer.",
    },
    {
      year: "2017",
      doing: "Worked intensively on ROMs & Kernels.",
    },
    {
      year: "2018",
      doing: "Learned Android app development.",
    },
    {
      year: "2019",
      doing: "Finished Diploma in Computer Engineering.",
    },
    {
      year: "2020",
      doing: "Acquired new skills while trying not to catch COVID-19",
    },
    {
      year: "2021",
      doing: "Survived COVID-19 and got my first Job at Primebook.",
    },
    {
      year: "2022",
      doing: "Completed B.Tech in Information Technology.",
    },
    {
      year: "2023",
      doing: "Year under progress....",
    },
  ];

  return (
    <Box className="my-journey">
      <Container maxWidth="lg">
        <Typography variant="h5" className="my-journey-title">
          myJourney<span style={{ color: "#ffffff" }}>()</span>
        </Typography>
        <Typography paragraph className="my-journey-subtitle">
          Throughout my journey, I've gained a wealth of knowledge and
          experience in both development and life in general. Im always eager
          to learn more and take on new challenges.{" "}
        </Typography>

        <Grid container spacing={1} mt={4}>
          {data.map((journey) => {
            return (
              <Grid item xs={2}>
                <Box>
                  <Box display={"flex"}  justifyContent={"space-between"}>
                    <Typography  className="my-journey-list-title">
                      {journey.year}
                    </Typography>
                    <Box className="my-journey-list-line"></Box>
                  </Box>
                  <Typography className="my-journey-list-subtitle">
                    {journey.doing}
                  </Typography>
                </Box>
              </Grid>
            );
          })}
        </Grid>
      </Container>
    </Box>
  );
};

export default MyJourney;
